
    <template>
      <section class="market-doc-container">
        <h2>Pagination 分页</h2>
<h3>基本使用</h3>
<mku-example>
        
        <template slot="source"><element-demo0 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
    &lt;mku-pagination :total=&quot;55&quot;&gt;&lt;/mku-pagination&gt;&lt;br/&gt;
    &lt;mku-pagination :total=&quot;77&quot;&gt;&lt;/mku-pagination&gt;&lt;br/&gt;
    &lt;mku-pagination :total=&quot;10000&quot;&gt;&lt;/mku-pagination&gt;&lt;br/&gt;
    &lt;mku-alert
      title=&quot;提示&quot;
      content=&quot;小于7页的时候，默认全部展示；7-8页的时候只会有一个省略号展示；大于等于9页的时候，根据当前选中的情况展示一个或两个省略号&quot;
    &gt;&lt;/mku-alert&gt;
  &lt;/div&gt;
&lt;/template&gt;
</code></pre></template></mku-example><h3>简约类型</h3>
<mku-example>
        
        <template slot="source"><element-demo1 /></template>
