<td style="text-align:left">-</td>
<td style="text-align:left"><code>1Í</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">type</td>
<td style="text-align:left">分页器的类型</td>
<td style="text-align:left"><code>String</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left"><code>simple | default | conjoin</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">size</td>
<td style="text-align:left">分页器的尺寸</td>
<td style="text-align:left"><code>String</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left"><code>small | default | large</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">show-total</td>
<td style="text-align:left">显示总数</td>
<td style="text-align:left"><code>Boolean</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left"><code>false</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">show-jumper</td>
<td style="text-align:left">显示快捷跳转控件</td>
<td style="text-align:left"><code>Boolean</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left"><code>false</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">show-page-size</td>
<td style="text-align:left">显示每页展示条数控件</td>
<td style="text-align:left"><code>Boolean</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left"><code>false</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">page-sizes</td>
<td style="text-align:left">每页展示条数控件的配置</td>
<td style="text-align:left"><code>Array</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left"><code>[10, 20, 50, 100]</code></td>
<td style="text-align:left">否</td>
</tr>
</tbody>
</table>
<h3>Pagination Events</h3>
<table>
<thead>
<tr>
<th style="text-align:left">事件名</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">回调函数参数</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">current-change</td>
<td style="text-align:left">current发生变化时触发</td>
<td style="text-align:left">当前current</td>
</tr>
<tr>
<td style="text-align:left">prev-click</td>
<td style="text-align:left">点击上一页按钮时触发</td>
<td style="text-align:left">当前current</td>
</tr>
<tr>
<td style="text-align:left">next-click</td>
<td style="text-align:left">点击下一页按钮时触发</td>
<td style="text-align:left">当前current</td>
</tr>
<tr>
<td style="text-align:left">prev-mutiple-click</td>
<td style="text-align:left">点击上一多页跳转按钮时触发</td>
<td style="text-align:left">当前current</td>
</tr>
<tr>
<td style="text-align:left">next-mutiple-click</td>
<td style="text-align:left">点击下一多页页跳按钮时触发</td>
<td style="text-align:left">当前current</td>
</tr>
<tr>
<td style="text-align:left">page-size-change</td>
<td style="text-align:left">切换每页展示条数时触发</td>
<td style="text-align:left">当前current</td>
</tr>
</tbody>
</table>
<h3>Pagination Slots</h3>
<table>
<thead>
<tr>
<th style="text-align:left">名称</th>
<th style="text-align:left">说明</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">prev</td>
<td style="text-align:left">自定义上一页的内容</td>
</tr>
<tr>
<td style="text-align:left">next</td>
<td style="text-align:left">自定义下一页的内容</td>
</tr>
</tbody>
</table>

